"use server";

import fs from 'fs';
import path from 'path';
import { GroceryItem, prisma } from '@/lib/items';
import { revalidatePath } from 'next/cache';

const ORDER_FILE = path.join(process.cwd(), 'data/current-order.json');

export async function saveOrder(items: GroceryItem[], orderId?: number) {
    try {
        // 1. Save to JSON (Legacy support)
        fs.writeFileSync(ORDER_FILE, JSON.stringify(items, null, 2));

        // 2. Save to Database
        if (items.length > 0) {
            if (orderId) {
                // UPDATE existing order
                await prisma.$transaction(async (tx) => {
                    // Update total items
                    await tx.order.update({
                        where: { id: orderId },
                        data: { totalItems: items.reduce((sum, i) => sum + (i.quantity || 1), 0) }
                    });

                    // Delete existing items
                    await tx.orderItem.deleteMany({
                        where: { orderId: orderId }
                    });

                    // Re-create items
                    for (const item of items) {
                        await tx.orderItem.create({
                            data: {
                                orderId: orderId,
                                itemId: item.id!, // Assuming ID exists for DB items
                                itemName: item.name,
                                quantity: item.quantity || 1
                            }
                        });
                    }
                });
            } else {
                // CREATE new order
                await prisma.order.create({
                    data: {
                        totalItems: items.reduce((sum, i) => sum + (i.quantity || 1), 0),
                        items: {
                            create: items.map(item => ({
                                itemId: item.id!,
                                itemName: item.name,
                                quantity: item.quantity || 1
                            }))
                        }
                    }
                });
            }
        }

        revalidatePath('/'); // Refresh dashboard
        return { success: true };
    } catch (e) {
        console.error("Failed to save order:", e);
        return { success: false, error: 'Failed to save' };
    }
}

export async function deleteOrder(orderId: number) {
    try {
        // Delete order (cascade will delete OrderItems automatically if configured)
        await prisma.order.delete({
            where: { id: orderId }
        });

        revalidatePath('/');
        return { success: true };
    } catch (e) {
        console.error("Failed to delete order:", e);
        return { success: false, error: 'Failed to delete' };
    }
}

export async function getOrders() {
    try {
        const orders = await prisma.order.findMany({
            orderBy: { createdAt: 'desc' },
            include: {
                items: true
            }
        });
        return orders;
    } catch (e) {
        console.error("Failed to fetch orders:", e);
        return [];
    }
}

export async function getOrder(id: number) {
    try {
        const order = await prisma.order.findUnique({
            where: { id },
            include: { items: { include: { item: true } } }
        });

        console.log('getOrder raw result:', JSON.stringify(order, null, 2));

        if (!order) return null;

        const result = {
            id: order.id,
            totalItems: order.totalItems,
            createdAt: order.createdAt,
            items: order.items.map(oi => ({
                id: oi.item.id,
                name: oi.item.name,
                tamilName: oi.item.tamilName || undefined,
                imageUrl: oi.item.imagePath || undefined,
                quantity: oi.quantity
            }))
        };

        console.log('getOrder mapped result:', JSON.stringify(result, null, 2));

        return result;
    } catch (e) {
        console.error("Failed to fetch order:", e);
        return null;
    }
}
